.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  margin:auto;
  height:50%;
  width: 60%;
}
.title{
  display:flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.underline{
  width: 20%;
  margin:0 auto;
  height:2px;
  background-color:rgb(0, 136, 255);
  margin-bottom:20px
}
.content{
 text-align:center;
}
.content h2{
  color:blue
}
.img_container{
  text-align:center;
}
img{
  height:260px;
  width: 260px;
  border-radius:50%;
  text-align:center;
}
.button-container{
  text-align:center;
}